import React, { useState, useRef, useEffect } from "react";
import { FormControl } from "baseui/form-control";
import { Input } from "baseui/input";
import { Slider } from "baseui/slider";
import { Checkbox } from "baseui/checkbox";
import { ProgressBar } from "baseui/progress-bar";
import { ProgressSteps, NumberedStep } from "baseui/progress-steps";
import { generate as generatePassword } from "generate-password";
import zxcvbn from "zxcvbn";

const MAXLENGTH = 64;
const MINLENGTH = 4;

export default () => {
  const [length, setLength] = useState(~~(MAXLENGTH / 2));
  const [uppercase, setUppercase] = useState(false);
  const [numbers, setNumbers] = useState(false);
  const [symbols, setSymbols] = useState(false);
  const [copied, setCopied] = useState(false);
  const [strength, setStrength] = useState(null);
  const [password, setPassword] = React.useState("");

  const setNewPassword = (p) => {
    const newPassword = p
      ? p
      : generatePassword({ length, numbers, uppercase, symbols });
    const obj = zxcvbn(newPassword);
    setStrength(obj.score);
    console.log("SCORE:", obj.score, " newPassword:", newPassword);
    setCopied(false);
    setPassword(newPassword);
  };

  useEffect(() => {
    setNewPassword(password);
  }, [length, uppercase, symbols, numbers, strength]);

  return (
    <div>
      <FormControl
        label={() => "Password"}
        caption={() => "Choose a strong password"}
      >
        <Input
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          type="password"
          placeholder="Enter password"
          clearable
          clearOnEscape
        />
      </FormControl>
      <ProgressBar value={strength - 1} successValue={3} />;
      <ProgressSteps current={strength}>
        <NumberedStep title="Bad"></NumberedStep>
        <NumberedStep title="Medium"></NumberedStep>
        <NumberedStep title="Strong"></NumberedStep>
        <NumberedStep title="Strongest"></NumberedStep>
      </ProgressSteps>
      <Slider
        min={MINLENGTH}
        max={MAXLENGTH}
        value={[length]}
        onChange={({ value }) => setLength(value[0])}
      />
      <FormControl label="Characters">
        <div>
          <Checkbox
            checked={uppercase}
            onChange={() => setUppercase(!uppercase)}
          >
            A-Z
          </Checkbox>
          <Checkbox checked={numbers} onChange={() => setNumbers(!numbers)}>
            0-9
          </Checkbox>
          <Checkbox checked={symbols} onChange={() => setSymbols(!symbols)}>
            %@#
          </Checkbox>
        </div>
      </FormControl>
    </div>
  );
};
